body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navigation {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  background: black;
  color: white;
  padding: 2em; }
  .navigation a {
    color: white; }

.footer {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  background: black;
  color: white;
  padding: 2em; }
  .footer a {
    color: white; }

.MovieList {
  padding: 2em;
  background: #444;
  color: white; }
  .MovieList .movies {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    grid-gap: 2em; }

.movieCard {
  all: unset;
  cursor: pointer;
  background-size: cover;
  position: relative;
  padding: 1em; }
  .movieCard * {
    position: relative;
    z-index: 1; }
  .movieCard::before {
    content: '';
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.4);
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 0; }

.app {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  min-height: 100vh; }
  .app .main {
    -webkit-flex: 1 1;
            flex: 1 1; }

